import React from 'react';
import '.././App.css';
import { useState } from 'react';
import { cart } from '../redux/state';




const EatBlock = (props) => {
    let [count, setCount] = useState(1) // state(hook) счетчик
    let [price, setPrice] = useState(parseFloat(props.price))

    let plusCount = () => {
        setCount(count + 1);
        setPrice((parseFloat(props.price) * count) + (parseFloat(props.price)))
    }

    let minusCount = () => {
        setCount(count - 1)
        setPrice(price - parseFloat(props.price))

        if (count <= 1) {
            setCount(count = 1)
            setPrice(parseFloat(props.price))
        }
    }



    function addEat() {
        alert("Вы добавили " + props.title + ", в количестве " + count);

        let objEat = {
            titleCart: props.title,
            countCart: count,
            priceCart: price,
        }

        cart.push(objEat);

    }
    
    
    return(
            <div className="eat_block">
                        <img src={props.img} alt="" />
                        <div className="title">{props.title}</div>
                        <div className="par">{props.par}</div>
                        <div className="add">
                            <p className="price">{price}$</p>
                            <div className="counter">
                                <div className="minus" onClick = {minusCount}>-</div>
                                <div className="current_count">{count}</div>
                                <div className="plus" onClick = {plusCount}>+</div>
                            </div>
                            <div className="buy" onClick = {addEat}>+</div>
                        </div>
            </div>
    )
};
// export { addCart };
export default EatBlock;