import React from 'react';
import '.././App.css';
import Logo from './logo';
import Cart from './cart';
import Nav from './nav';

const Header = () => {
    
    return(
            <header>
                <div className="content">
                    <div className="header_wrapper">
                        <Logo />
                        <Nav/>
                        <Cart />
                    </div>
                </div>
            </header>
    )
};

export default Header