import React from 'react';
import '.././App.css';


const Post =(props)=> {

    return (
        <div className="content">
                <div className="post_block">
                    <div className="post_name">{props.name}</div>
                    <p className="post_text">{props.message}</p>
                    {/* <div className="likes">Лайки: {props.likes}
                        <button className="like">+Like</button> */}
                    {/* </div> */}
                </div>
        </div>
    )
};

export default Post