import React from 'react';
import '.././App.css';
import SectionFoodName from './sectionfoodname.jsx';
import SectionEat from './sectioneat';


const SectionFood = (props) => {
    
    return(
            <section>
                <div className="content">
                    <div className="section_wrapper">
                        <SectionFoodName name = "Burgers" />
                        <SectionEat />
                    </div>
                </div>
            </section>
    )
};

export default SectionFood