import React from 'react';
import '.././App.css';
import EatBlock from './eatblock';
import { burgers } from '../redux/state';


const SectionEat = (props) => {

let burgerBlock = burgers.map(b => <EatBlock img = {b.img_burger} title = {b.title_burger} par = {b.par_burgers} price = {b.price_burgers}/>)


    return(
            <div className="wrapper_eat">
                {burgerBlock}
            </div>
    )
};

export default SectionEat