import React from 'react';
// import logo from './logo.svg';
import './App.css';
import Header from './components/header';
import SectionFood from './components/sectionfood';
import CartClick from './components/cartclick';
import ModalClick from './components/modalclick';
import Otzivi from './components/otzivi';
import Index from './components';
import { BrowserRouter, Route } from 'react-router-dom';


function App() {
  return (
    <BrowserRouter>
    <div className="App">
    <Route path = "/menu/cart" component = {CartClick}/>
      {/* <ModalClick />  */}
      <Header />
      <Route path = "/index" component = {Index}/>
      <Route path = "/menu" component = {SectionFood}/>
      <Route path = "/otzivi" render = {  () => <Otzivi />}/>
    </div>
    </BrowserRouter>
  );
}

export default App;
