import React from 'react';
import '.././App.css';
import Post from './post';
import {posts} from "../redux/state"
import { useState } from 'react';
 

const Otzivi = (props) => {

    let [idPost, setIdPost] = useState(posts)

    let namePostElement = React.createRef();
    let newPostElement = React.createRef();

    let post = posts.map(p => <Post name = {p.name} message = {p.msg} likes = {p.like}/>);
    // let post2 = posts.map(p => <Post name = {p.name} message = {p.msg} likes = {p.like}/>);
    
    
    function addPost() {
        let namePostText = namePostElement.current.value;
        let postText = newPostElement.current.value;

        let postObj = {
            id: posts.length + 1,
            name: namePostText,
            msg: postText,
            like: false,
        }
        setIdPost(posts.unshift(postObj));


        console.log(posts);
        
        
    //     if (true) {
    //     return (
    //         <div className="content">

    //         <p class ="p_name_page">Отзывы о нас</p>
    //         <hr id = "hr1"></hr>

    //         <div className="wrapper_eat">
    //             <form action="">
    //                 <input ref = { namePostElement } type="text" placeholder = "Ваше имя"/>
    //                 <textarea ref = { newPostElement } type="text" placeholder = "Напишите Ваше мнение о нас"/>
    //                 <div className="button" onClick = {addPost}>Добавить отзыв</div>
    //             </form>
    //             {post2}
    //         </div>
    //     </div>
    //     );
    // }
}


    return(
        <div className="content">

            <p class ="p_name_page">Отзывы о нас</p>
            <hr id = "hr1"></hr>

            <div className="wrapper_eat">
                <form action="">
                    <input ref = { namePostElement } type="text" placeholder = "Ваше имя"/>
                    <textarea ref = { newPostElement } type="text" placeholder = "Напишите Ваше мнение о нас"/>
                    <div className="button" onClick = {addPost}>Добавить отзыв</div>
                </form>
                {post}
            </div>
        </div>
    )


};

export default Otzivi