import React from 'react';
import '.././App.css';
import OrderBlock from './orderblock';
import { NavLink } from 'react-router-dom';
import { cart } from '../redux/state';
import { useState } from 'react';




const CartBlock = (props) => {

   
let cartItem = cart.map(i => <OrderBlock nameCart = {i.titleCart} counterCart = {i.countCart} priceCart = {i.priceCart} />)

let [finalSum, setFinalSum] = useState(0);

    
function updateSum() {
    for (let i = 0; i < cart.length; i++) {
        finalSum += parseFloat(cart[i].priceCart);
    }
    setFinalSum(finalSum);

}

    return(
            <div className="cart_block">
                <p id = "order_name">Ваш заказ</p>
                {cartItem}
        

                <div id="order_go">Оформить Заказ</div>
                <div id="order_go update" className = "update" onClick = {() => updateSum()}>Обновить Итого</div>

                <p id = "total"> Итого: 
                
                <span id = "sum">{finalSum}$</span>
                
                </p>
                <NavLink to = "/menu"><div id="close">x</div></NavLink>
            </div>
    )
};

export default CartBlock